@import '../base/variables.sass';

.btn {
    background-color: $sec-color;
    color: $accent-one;
    text-decoration: none;
    padding: 0.75rem 1.2rem;
    display: inline-block;

    &--accent-two {
        background-color: $accent-two; }


    &--large {
        font-size: 0.9rem;
        padding: 0.8rem 1.2rem;

        @include at-medium {
            font-size: 1.2rem;
            padding: 1rem 1.6rem; }

        @include at-large {
            font-size: 1.3rem;
            padding: 1.2rem 1.8rem; }

        @include at-xlarge {
            font-size: 1.3rem;
            padding: 1.2rem 2rem; } } }

